.Events{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(245, 222, 179, 0.85);;
}

.Heading{
    font-size: 5.4rem;
}

@media (max-width: 898px) {

    .Heading{
        font-size: 4.4rem;
    }
    
}


@media (max-width: 437px) {

    .Heading{
        font-size: 3.4rem;
    }
    
}


@media (max-width: 334px) {

    .Heading{
        font-size: 2.4rem;
    }
    
}


@media (max-width: 249px) {

    .Heading{
        font-size: 1.7rem;
    }
    
}