} */

.FounderSection{
    /* scroll-snap-align: start; */
    width: 100vw;
    display: flex;
    justify-content: center;
    gap: 50px;
    align-items: center;
    height: 100vh;
    /* background-color: rgba(154, 184, 192, 0.8); */
    background-color: rgba(245, 222, 179, 0.85);
}

.FounderSection_tab{
    display: none;
}

.photo{
    border-radius: 20px;
    border: 2px solid black;
    max-width: 400px;
}

.photo > img{
    max-width: 100%;
    height: auto;
  
}

.content{
    width: 770px;
    height: 510px;
    display: flex;
    flex-direction: column;
    /* background-color: #3791aa; */
}

.Title{
    margin-top: 20px;
    height: 50px;
    margin-left: 20px;
    color: #454444;
    font-weight: 700;
    font-size: 2.5rem;
    margin-bottom: 20px;
}

.Logos{
    display: flex;
    align-items: center;
    margin-left: 20px;
    gap: 32px;
    /* width: 60%;
    flex-wrap: wrap;
    min-height: 300px; */
    margin-bottom: 20px;
}

.FounderContent{
    width: 85%;
    font-size: 1.4rem;
    border: 2px solid rgb(35, 35, 35);
    height: 65%;
    border-radius: 20px;
    padding: 10px;
}

.BoardmemberSection{
    /* scroll-snap-align: start; */
    width: 100vw;
    height: 100vw;
    background-color: rgba(245, 222, 179, 0.6);
}

.BoardTitle{
    font-size: 3rem;
    margin-top: 40px;
    margin-left: 90px;
    font-weight: 900;
}

.BoardMemebrContainer{
    width: 90%;
    margin-top: 20px;
    gap: 20px;
    height: 88%;
    display: flex;
    flex-wrap: wrap;
    /* border: 2px solid red; */
    margin-left: 90px;
}

@media (max-width: 898px) {
    .FounderSection{
        gap: 30px;
    }

    .photo{
        margin-left: 20px;
    }

    .Title{
        padding-bottom: 5px;
        /* min-height: 150px; */
        font-size: 1.8rem;
    }

    .Logos{
        flex-wrap: wrap;
        width: 80%;
        /* height: fit-content; */
        /* border: 2px solid red; */
    }

    .BoardMemebrContainer{
        margin-bottom: 20px;
    }
}



/* tablet */
    .FounderSection_mobile{
        display: none;
    }

@media (max-width: 798px) {

    .FounderSection{
        display: none;
    }

    .FounderSection_tab{
        display: block;
        background-color: rgba(245, 222, 179, 0.85);

    }

    .contentSectionOne_tab{
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 30px;
    }

    .photo_tab{
        margin-top: 80px;
        margin-left: 30px;
        object-fit: cover;
        border: 2px solid black;
        border-radius: 20px;
        max-width: 100%;
    }

    .photo_tab > img{
        max-width: 100%;
        height: auto;
    }

    .titleAndLogo{
        /* border: 2px solid red; */
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-top: 80px;
        flex-wrap: wrap;
        padding: 40px;
        gap: 50px;
        /* height: 400px; */
    }

    .Logos_tab{
        display: flex;
        align-items: centerf;
        flex-wrap: wrap;
        gap: 15px;
        
    }

    .content_tab{
        border: 2px solid black;
        width: 80%;
        margin-left: 25px;
        border-radius: 20px;
        height: 300px;
        padding: 20px;
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .BoardmemberSection{
        display: flex;
        align-items: center;
        flex-direction: column;
        margin-left: 0;
    }

    .BoardTitle{
        margin-left: 0;
    }

    .BoardMemebrContainer{
        align-items: center;
        justify-content: center;
        margin-left: 0;
    }
}

@media (max-width: 580px) {
    .FounderSection_tab{
        display: none;
    }

    .FounderSection_mobile{
        display: block;
        background-color: rgba(245, 222, 179, 0.85);
    }

    .contentSectionOne_mobile{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .photo_mobile{
        margin-top: 80px;
        object-fit: cover;
        border: 2px solid black;
        border-radius: 20px;
        max-width: 70%;
    }

    .photo_mobile > img{
        max-width: 100%;
        height: auto;
    }

    .Title_mobile{
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .Logos_mobile{
        width: 70%;
        /* border: 2px solid red; */
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 25px;
    }

    .Logos_mobile > img{
        margin: 5px;
    }

    .content_mobile{
        /* border: 2px solid black; */
        display: flex;
        justify-content: center;
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .FounderContent_tab{
        width: 80%;
        border: 2px solid black;
        border-radius: 20px;
        height: 300px;
    }

    .BoardmemberSection{
        min-height: 800px;
    }
}

@media (max-width: 393px) {

    .Title{
        font-size: 1.8rem;
    }

    .BoardTitle{
        font-size: 1.7rem;
    }

    
}

@media (max-width: 356px) {

    .Title_mobile{
        font-size: 1.4rem;
    }
    
}